body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background-image: url(/lim-2018-01-burger-queen/static/media/img-fond.e21b647d.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  margin: 0;
  padding: 5%;
  height: 100vh;
}


.bodyInit{
  background: #f1f0f0;
  padding-top: 40px;
  width:100%;
  height: 400px;
  margin-top: 10%
}

.one{
  height: 100%;
}

i{
  color: #000000;
  cursor: pointer;
  margin-left: 5px;
}



h1 {
  font-family: 'Fredoka One', cursive;
  text-align: center;
  margin-bottom: 20px;
  font-size: 60px;
  font-weight: 1000;
  color: rgb(255, 209, 57);
}

Button {
  margin-left: 10px;
  margin-top: 10px;
  float: center;
  width: 130px;
  margin-bottom: 10px;
}

.list{
  box-shadow: 10px 13px 5px -4px rgba(0,0,0,0.75);
}

h3 {
  color: #fff;
}

.inicio {
  background: #f1f0f0;
  color: black;
  width: 100%;
  height: 650px;
  padding: 20px;
  overflow: auto;
  margin-bottom: 20px;
}

p {
  color: black;
}


@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.cont{
    background: #fcd65c;
    border-radius:15px;
    width: 35%;
    height: 7%;
    border:none;
    cursor: pointer;
    box-shadow: 6px 9px 5px -4px rgba(0,0,0,0.75);
}

p{
    font-weight: 600;
}
.contain{
    background: rgb(255, 209, 57);
    border-radius:15px;
    width: 35%;
    height: 7%;
    border:none;
    cursor: pointer;
    box-shadow: 6px 9px 5px -4px rgba(0,0,0,0.75);
}
p{
    font-weight: 600;
}

